import React from 'react';

class Sidenav extends React.Component {

    static toggleShow(menuState) {
        let sideNav = document.getElementById("sdnv");

        if(!menuState) sideNav.style.width = "350px"
        else sideNav.style.width = "0";
    }

    render() {
        return (
            <div id = "sdnv" class = "sidenav">
                <p>02-1</p>
            </div>
        );
    }
}

export default Sidenav;
